.carrusel{
    width: 100%;
    /* margin: 0px 60px; */
}
.carrusel h2{
    font-size: 26px;
    line-height: 38px;
    padding-bottom: 24px;
    opacity: .9;
    text-transform: uppercase;
    letter-spacing: 10px;
    text-align: center;
}

/* images */

.tarjeta-list{
    position: relative;
    display: flex;
    align-items: center;
    width: fit-content;
    height: 304px;
    padding: 10px 0px;
    margin: 0px auto;
    max-width: 90vw;
    overflow: hidden;
}
.tarjeta-track{
    position: relative;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    transition: .5s ease-in-out;
}
.tarjeta{
    position: relative;
    width: 275px;
    padding: 0 18px;
    float: left;
    box-sizing: border-box;
    display: flex;
    height: 100%;
}
.tarjeta h4{
    position: absolute;
    z-index: 1;
    font-size: 22px;
    line-height: 23px;
    color: #fff;
    padding: 15px;
}
.tarjeta h4 small{
    font-size: 15px;
    display: block;
}
.tarjeta a img{
    object-fit: cover;
    /* height: 300px; */
    width: 100%;
    border-radius: 4px;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.5);
    transition: .3s ease-in-out;
}
.tarjeta a img:hover{
    opacity: .85;
}

/* buttons */

.tarjeta-arrow{
    border-radius: 30px;
    background-color: #fff;
    position: absolute;
    z-index: 4;
    width: 48px;
    height: 48px;
    text-align: center;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.15);
    border: 0;
    cursor: pointer;
    font-size: 30px;
}
.tarjeta-arrow:focus{
    outline: 0;
}
.tarjeta-arrow svg{
    width: 12px;
    height: 100%;
    color: rgba(0,0,0,.7);
}
.tarjeta-prev{
    left: 0px;
}
.tarjeta-next{
    right: 0px;
}